.modal-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-box {
  width: 40%;
  height: auto;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  z-index: 1001;
  animation: modal 0.3s ease-in-out;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  @media only screen and (max-width: 1024px) {
    width: 80%;
  }
}
.modal-inner{
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  img{
    width: 100%;
    height: 100%;
  }
  .close-icon {
    position: absolute;
    left: 10px;
    top: 10px;
    color: #fff;
    opacity: .7;
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    @media only screen and (max-width: 1024px) {
      width: 1.2rem;
      height: 1.2rem;
    }
  }
}
.modal-mask {
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.53);
  position: fixed;
  z-index: 1001;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  animation: modal 0.3s ease-in-out;
}

@keyframes modal {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
